html{
  box-sizing: content-box;
}

html *{
  box-sizing: inherit;
}

header{
  display: flex;
  justify-content: center;
  background-color: #fff;
  padding: 22px;
  box-shadow: 1px 2px 5px rgb(0,0,0,0.3);
  z-index: 2;
  position: relative;
}

header input {
  outline: none;
  border: solid 1px #eee;
  padding: 7px 5px;
  border-radius: 5px;
}

.container{
  display: flex;
  justify-content: center;
  background-color: #eee;
}

.center{
  column-count: 3;
  width: 1350px;
}
article{
  width: 370px;
  display: inline-block;
  transition: box-shadow 0.2s ease;
  margin: 40px 10px 0 10px;
  background-color: #fff;
  border-radius: 8px;
  cursor: pointer;

}
article > img{
  border-radius: 5px 5px 0 0;
  width: inherit;
}

article > p{
    margin: 10px 15px;
}

article:hover{
     box-shadow: 0 3px 5px rgb(0,0,0,0.2);
}

.container_two{
  width: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  background-color: #fff;
}

